@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  font-style: bold;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-display: swap;
}
.reset-list-style {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.reset-style {
  padding: 0;
  margin: 0;
}
.page {
  height: 100%;
  background-color: #e7e5e5;
}
.page__body {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
  padding: 0;
  margin: 0;
  text-transform: uppercase;
  text-align: center;
  color: #000000;
}
.page__container {
  background-color: #ffffff;
  margin: 0 auto;
  max-width: 1200px;
  min-width: 320px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
.main-header {
  position: relative;
  display: flex;
  flex-direction: column;
}
.main-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
  opacity: 1;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.7;
}
.main-header__logo:focus-visible {
  outline: none;
  box-shadow: 0 0 6px 2px #333333;
}
.main-nav {
  width: 100%;
}
.main-nav__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 25px;
  background-color: #81b3d2;
  color: #ffffff;
  position: relative;
}
.main-nav__link {
  padding-top: 17px;
  padding-bottom: 12px;
  display: block;
  text-decoration: none;
  color: inherit;
}
.main-nav__toggle {
  position: absolute;
  z-index: 1;
  top: 17px;
  right: 20px;
  width: 23px;
  height: 22px;
  border: none;
  cursor: pointer;
  opacity: 1;
}
.main-nav__toggle:hover {
  opacity: 0.6;
}
.main-nav__toggle:active {
  opacity: 0.3;
}
.main-nav__toggle:focus-visible {
  outline: none;
  box-shadow: 0 0 6px 2px #333333;
}
.main-nav--closed .main-nav__toggle {
  background: url("../img/icon-open-menu.svg") center no-repeat transparent;
}
.main-nav--opened .main-nav__toggle {
  background: url("../img/icon-close-menu.svg") center no-repeat transparent;
}
@media (max-width: 767px) {
  .main-nav {
    height: 56px;
  }
  .main-nav--opened {
    z-index: 3;
  }
  .main-nav--nojs {
    position: static;
    margin-top: 95px;
    display: block;
    height: auto;
  }
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__item {
    display: block;
  }
  .main-nav__item {
    width: 100%;
  }
  .main-nav__item:not(:last-child) {
    border-bottom: 1px solid #668bab;
  }
  .main-nav__item:not(:first-child) {
    border-top: 1px solid #98bfde;
  }
  .main-nav__item:last-child {
    box-shadow: inset 0 -3px 4px rgba(1, 1, 1, 0.2);
  }
  .main-nav__item:hover,
  .main-nav__item:focus-visible {
    background-color: #669ec0;
  }
  .main-nav__item:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    padding-top: 19px;
    padding-bottom: 16px;
  }
  .main-nav__link--current {
    color: #766357;
  }
  .main-nav__link:hover {
    color: #81b3d2;
  }
  .main-nav__link:focus-visible {
    outline: none;
    box-shadow: inset 0 0 6px #333333;
  }
  .main-nav__link:active {
    color: #b3b3b3;
    box-shadow: none;
    outline: none;
  }
  .main-nav__list {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
    background-color: #ffffff;
    color: #000000;
    display: grid;
    grid-template-columns: repeat(5, 1fr);
  }
  .main-nav__item:nth-child(4n+3) {
    grid-column: 4/5;
  }
  .main-nav__item:nth-child(4n+4) {
    grid-column: 5/6;
  }
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    max-width: 1200px;
  }
}
.main-footer {
  padding: 28px 20px 10px 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
