.social__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 6px;
}
.social__link {
  width: 48px;
  height: 48px;
  position: relative;
}
.social__link::before {
  display: block;
  content: "";
  width: 24px;
  height: 24px;
  background-repeat: no-repeat;
  background-position: center center;
  position: absolute;
  top: 12px;
  left: 12px;
}
.social__link--vkontakte::before {
  background-image: url("../img/icon-vk.svg");
}
.social__link--telegram::before {
  background-image: url("../img/icon-telegram.svg");
}
.social__link--youtube::before {
  background-image: url("../img/icon-youtube.svg");
}
.social__link:active::before {
  opacity: 0.3;
}
.copyright__link {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: #000000;
  padding-bottom: 3px;
}
.copyright__link:hover {
  color: #81b3d2;
}
.copyright__link:active {
  color: #b3b3b3;
}
.copyright__link:focus-visible {
  outline: none;
  box-shadow: 0 0 6px 2px #333333;
}
.copyright__text {
  padding-right: 12px;
}
.button {
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  font-family: inherit;
  display: block;
  background-color: #81b3d2;
  border: none;
  text-transform: uppercase;
  color: #ffffff;
  opacity: 1;
  cursor: pointer;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:focus-visible {
  outline: none;
  box-shadow: 0 0 4px 1px #333333;
}
.button:disabled {
  background-color: #e5e5e5;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@media (min-width: 768px) {
  .text-wrap::before {
    content: "\A";
    white-space: pre;
  }
}
@media (min-width: 1200px) {
  .text-wrap-desktop::before {
    content: "\A";
