.input-text {
  display: flex;
  flex-direction: column;
  margin-bottom: 16px;
}
.input-text__label {
  margin-left: 16px;
  color: #333333;
  text-align: start;
}
.input-text__input {
  font-weight: 700;
  font-size: 16px;
  line-height: 30px;
  width: 100%;
  padding: 16px 16px 12px 15px;
  box-sizing: border-box;
  border: none;
  text-transform: none;
  background-color: #f2f2f2;
  background-repeat: no-repeat;
  background-position: right 20px center, right center;
}
.input-text__input--phone {
  background-image: url("../img/icon-phone.svg"), linear-gradient(to left, #e6e6e6 58px, transparent 58px);
  padding-right: 62px;
}
.input-text__input--email {
  background-image: url("../img/icon-email.svg"), linear-gradient(to left, #e6e6e6 58px, transparent 58px);
  padding-right: 62px;
}
.input-text__input:hover {
  background-color: #e5e5e5;
}
.input-text__input:active {
  background-color: #ffffff;
  box-shadow: inset 0 0 4px 1px #e5e5e5;
}
.input-text__input:focus-visible {
  background-color: #ffffff;
  box-shadow: inset 0 0 4px 1px #e5e5e5;
  outline: 2px solid #e5e5e5;
}
.input-radio__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  min-height: 45px;
  padding-left: 61px;
  position: relative;
  display: flex;
  align-items: center;
  text-transform: none;
  cursor: pointer;
}
.input-radio__label::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  border-radius: 50%;
  background-color: #f2f2f2;
}
.input-radio__label::after {
  content: "";
  width: 16px;
  height: 16px;
  position: absolute;
  top: 15px;
  left: 15px;
  border-radius: 50%;
  background-color: #333333;
  display: none;
}
.input-radio__label:hover::before {
  background-color: #e5e5e5;
}
.input-radio__label:active::before {
  background-color: #d5d5d5;
}
.input-radio__label:active::after {
  background-color: rgba(51, 51, 51, 0.3);
}
.input-radio__input:checked + .input-radio__label::after {
  display: block;
}
.input-radio__input:focus-visible ~ .input-radio__label::before {
  box-shadow: inset 0 0 4px 1px #d5d5d5;
  outline: 2px solid #d5d5d5;
}
@media (max-width: 767px) {
  .input-radio:not(:last-child) {
    margin-bottom: 18px;
  }
}
.input-checkbox {
  display: flex;
}
.input-checkbox__label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  padding-left: 63px;
  padding-top: 11px;
  min-height: 32px;
  position: relative;
  text-transform: none;
  cursor: pointer;
}
.input-checkbox__label::before {
  content: "";
  width: 46px;
  height: 46px;
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  background: #f2f2f2;
}
.input-checkbox__label::after {
  content: "";
  background: url("../img/tick.svg") no-repeat center;
  width: 22px;
  height: 16px;
  position: absolute;
  top: 13px;
  left: 12px;
  display: none;
}
.input-checkbox__label:hover::before {
  background-color: #e5e5e5;
}
.input-checkbox__label:active::before {
  background-color: #d5d5d5;
}
.input-checkbox__label:active::after {
  opacity: 0.3;
}
.input-checkbox__input:checked + .input-checkbox__label::after {
  display: block;
}
.input-checkbox__input:focus-visible ~ .input-checkbox__label::before {
  box-shadow: inset 0 0 4px 1px #d5d5d5;
  outline: 2px solid #d5d5d5;
}
@media (max-width: 767px) {
  .input-checkbox:not(:last-child) {
    margin-bottom: 5px;
  }
  .input-checkbox__label {
    min-height: 46px;
  }
}
@media (min-width: 1200px) {
  .input-checkbox {
    justify-content: center;
  }
